@use '../../Styles/vars.scss';
@use '../../Styles/index.scss';

.movieslist {

  &-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &-detail {
    margin-top: 1rem;
    height: 20rem;
    overflow-y: scroll;

    .list-group {

      &-item {
        height: 3rem;
        padding: 0.5rem;
        background-color: vars.$background-secondary-color;
        border-radius: vars.$input-border-radius;
        border: 1px solid vars.$background-third-color;
      }
    }
  }

 
}

.poster-figure {
  width: 128px;
  height: 192px;
}
.more-results-button {
  width: 10rem;
  height: 2rem;
  font-size: vars.$text-size;
  background-color: vars.$button-color;
  font-family: vars.$text-font;
  color: vars.$background-secondary-color;
  border: none;
  border-radius: vars.$button-border-radius;
  padding: 0 !important;
  align-self: center;
  margin-top: 2rem;
}